body .content .container .row .col-lg-3 .aside {
  margin: 0 0 30px;
}
body .content .container .row .col-lg-3 .aside__vhLogo {
  height: 105px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url("../img/vh_logo.jpg");
  background-size: cover;
}
body .content .container .row .col-lg-3 .aside__vhLogo p {
  padding: 0 0 0 130px;
  font: 2.438em/0.875em "BravoSC";
  color: #d10000;
}
body .content .container .row .col-lg-3 .aside__vhLogo p:nth-child(1) {
  font: 1.625em/0.875em "BravoSC";
  color: #d10000;
}
body .content .container .row .col-lg-3 .aside__menu {
  margin: 30px 0;
}
body .content .container .row .col-lg-3 .aside__menu ul li a {
  padding: 0 0 0 20px;
  display: block;
  font: 0.938em/30px "Arial";
  font-weight: bold;
  color: #0081ef;
  text-decoration: none;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__menu ul li a:hover {
  background-color: #d1efff;
}
body .content .container .row .col-lg-3 .aside__bestHolo,
body .content .container .row .col-lg-3 .aside__newHolo,
body .content .container .row .col-lg-3 .aside__threeD {
  margin: 30px 0;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__bestHolo a,
body .content .container .row .col-lg-3 .aside__newHolo a,
body .content .container .row .col-lg-3 .aside__threeD a {
  display: block;
  text-decoration: none;
}
body .content .container .row .col-lg-3 .aside__bestHolo a h2,
body .content .container .row .col-lg-3 .aside__newHolo a h2,
body .content .container .row .col-lg-3 .aside__threeD a h2 {
  padding: 0 0 0 15px;
  font: 0.938em/40px "Arial";
  font-weight: bold;
  color: #777;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__bestHolo a span,
body .content .container .row .col-lg-3 .aside__newHolo a span,
body .content .container .row .col-lg-3 .aside__threeD a span {
  display: block;
  padding: 20px 15px;
  font: 0.875em/1.25em "Arial";
  font-weight: bold;
  color: #0081ef;
  text-transform: uppercase;
}
body .content .container .row .col-lg-3 .aside__bestHolo a img,
body .content .container .row .col-lg-3 .aside__newHolo a img,
body .content .container .row .col-lg-3 .aside__threeD a img {
  width: 100%;
  padding: 15px;
}
body .content .container .row .col-lg-3 .aside__examples {
  margin: 30px 0;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples h2 {
  padding: 0 0 0 15px;
  font: 0.938em/40px "Arial";
  font-weight: bold;
  color: #777;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples ul li {
  display: flex;
  flex-wrap: wrap;
}
body .content .container .row .col-lg-3 .aside__examples ul li a {
  width: 50%;
  display: inline-block;
}
body .content .container .row .col-lg-3 .aside__examples ul li a img {
  width: 100%;
  padding: 10px;
}
body .content .container .row .col-lg-3 .aside__examples .controls {
  display: flex;
}
body .content .container .row .col-lg-3 .aside__examples .controls .prev,
body .content .container .row .col-lg-3 .aside__examples .controls .next {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font: 0.875em/40px "Arial";
  color: #0081ef;
  text-align: center;
  background-color: #eee;
  border-top: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__examples .controls .prev .dot,
body .content .container .row .col-lg-3 .aside__examples .controls .next .dot {
  font-size: .4em;
  color: #777;
}
body .content .container .row .col-lg-3 .aside__examples .controls .prev:hover,
body .content .container .row .col-lg-3 .aside__examples .controls .next:hover {
  cursor: pointer;
}
body .content .container .row .col-lg-3 .aside__custom {
  margin: 30px 0;
  border: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__custom h2 {
  padding: 10px 0 10px 15px;
  font: 0.938em/1.25em "Arial";
  font-weight: bold !important;
  color: #0081ef;
  text-transform: uppercase;
  background-color: #eee;
  border-bottom: 1px solid #c4c4c4;
}
body .content .container .row .col-lg-3 .aside__custom p {
  padding: 10px 15px;
  font: 0.875em/1em "Arial";
  color: #777;
}
body .content .container .row .col-lg-3 .aside__custom p a {
  color: #0081ef;
}
